@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Oswald Bold"), local("Oswald-bold"), url("../fonts/oswaldmedium.woff2") format("woff"), url("../fonts/oswaldmedium.woff") format("woff");
}
body {
  font-family: arial, helvetica, sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header" "main" "footer";
}

.page-header {
  grid-area: header;
  position: relative;
}

.page-main {
  grid-area: main;
}

.page-footer {
  position: relative;
  background-color: #f2f2f2;
}

.page-footer__wrapper {
  background-color: #f2f2f2;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    flex-direction: row;
    justify-content: space-between;
    padding: 30px;
    align-items: center;
  }
}
@media (min-width: 1220px) {
  .page-footer__wrapper {
    max-width: 1220px;
    margin: auto;
  }
}

.page-footer__logo {
  margin: auto;
  margin-top: 40px;
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .page-footer__logo {
    margin: 0;
  }
}

.page-footer__logo img:hover {
  opacity: 0.8;
}
.page-footer__logo img:active {
  opacity: 0.6;
}

.page-footer__partner {
  margin: auto;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #000000;
  font-size: 1.1rem;
  margin-top: 22px;
  margin-bottom: 22px;
  width: 100%;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .page-footer__partner {
    width: auto;
    margin: 0;
  }
}
.page-footer__partner:active {
  opacity: 0.3;
}
.page-footer__partner .logo__img-svg {
  fill: #222222;
}
.page-footer__partner .logo__img-svg:hover {
  fill: #68b738;
}
.page-footer__partner .logo__img-svg:active {
  opacity: 0.3;
}

.button {
  padding: 15px 80px;
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-size: 1.1rem;
  line-height: 1.1rem;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  display: block;
  text-align: center;
}

.button--neutral {
  color: #000000;
  background-color: #f2f2f2;
}

.contacts__wrapper {
  background-color: #f2f2f2;
  display: flex;
  padding: 28px 20px;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .contacts__wrapper {
    padding: 71px;
  }
}
@media (min-width: 1220px) {
  .contacts__wrapper {
    background-color: #ffffff;
    position: absolute;
    top: 20%;
    left: 16%;
    z-index: 3;
    width: 510px;
    padding: 66px 77px;
  }
}

.contacts__slogan {
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-size: 1.1rem;
  text-transform: uppercase;
  max-width: 171px;
}
@media (min-width: 768px) {
  .contacts__slogan {
    font-size: 1.2rem;
    line-height: 1.5rem;
  }
}
@media (min-width: 1220px) {
  .contacts__slogan {
    margin: 0;
  }
}

.contacts__address {
  line-height: 1.1rem;
  font-style: normal;
}
@media (min-width: 768px) {
  .contacts__address {
    font-size: 1.1rem;
    line-height: 1.1rem;
    text-align: right;
  }
}

.contacts-map {
  width: 100%;
}
@media (min-width: 1220px) {
  .contacts-map {
    z-index: 2;
  }
}

.contacts-map__image img {
  width: 100%;
  height: auto;
  vertical-align: bottom;
}

.container {
  max-width: 1220px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}
.container--form {
  display: block;
}
@media (min-width: 768px) {
  .container--form {
    padding-left: 70px;
    padding-right: 70px;
  }
}
@media (min-width: 1220px) {
  .container {
    padding-left: 110px;
    padding-right: 110px;
  }
}

.main-navigation {
  padding-left: 20px;
  padding-right: 20px;
  min-width: 280px;
  margin-left: auto;
  margin-right: auto;
  min-height: 65px;
  max-width: 1220px;
  background-color: #ffffff;
  position: relative;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .main-navigation {
    grid-template-columns: 1fr auto;
    padding-right: 30px;
    border: none;
  }
}
@media (min-width: 1220px) {
  .main-navigation {
    background-color: transparent;
  }
}

@media (min-width: 768px) {
  .main-navigation__logo-text {
    display: none;
  }
}

.main-navigation__toggle {
  background-color: #ffffff;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 65px;
  height: 65px;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 20px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  left: 23px;
  width: 25px;
  height: 1.5px;
  background-color: #000000;
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__site-navigation {
    display: none;
  }
}
.site-navigation {
  font-weight: bold;
  list-style: none;
  padding: 0;
  position: absolute;
  top: 51px;
  left: 0;
  background-color: #ffffff;
  width: 100%;
  border-bottom: 1px solid #f2f2f2;
  z-index: 1;
}
@media (min-width: 768px) {
  .site-navigation {
    position: static;
    display: flex;
    border-bottom: none;
  }
}
@media (min-width: 1220px) {
  .site-navigation {
    background-color: transparent;
  }
}

@media (min-width: 768px) {
  .site-navigation__item {
    margin-left: 34px;
  }
}

.site-navigation__link {
  color: #000000;
  display: block;
  padding-top: 24px;
  padding-bottom: 24px;
  font-size: 1.4rem;
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-weight: bold;
  line-height: 1.4rem;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  border-top: 1px solid #f2f2f2;
}
@media (min-width: 768px) {
  .site-navigation__link {
    border: none;
    font-weight: normal;
  }
  .site-navigation__link--active {
    border-bottom: 2px solid #68b738;
  }
  .site-navigation__link:hover {
    opacity: 0.6;
  }
  .site-navigation__link:active {
    opacity: 0.3;
  }
}

.site-navigation__link-promo--active {
  border-bottom: 2px solid #68b738;
}
@media (min-width: 1220px) {
  .site-navigation__link-promo {
    color: #ffffff;
  }
  .site-navigation__link-promo--active {
    border-bottom: 2px solid #ffffff;
  }
  .site-navigation__link-promo:hover {
    opacity: 0.6;
  }
  .site-navigation__link-promo:active {
    opacity: 0.3;
  }
}

.social {
  list-style: none;
  display: flex;
  padding: 39px 0;
  justify-content: space-evenly;
  align-items: center;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .social {
    padding: 0;
    width: 129px;
    border: none;
  }
}
.social .logo__img-svg {
  fill: #000000;
  stroke: #000000;
}
.social .logo__img-svg:hover {
  fill: #68b738;
  stroke: #68b738;
}
.social .logo__img-svg:active {
  opacity: 0.3;
}

.features {
  margin: 20px;
}
@media (min-width: 768px) {
  .features {
    padding: 0 30px;
    margin: auto;
  }
}
@media (min-width: 1220px) {
  .features {
    margin: auto;
    max-width: 1220px;
  }
}

.features__title {
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-weight: normal;
  font-size: 2.6rem;
  line-height: 2.2rem;
  margin-bottom: 38px;
}

.features__list {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: 1fr 1fr;
    gap: 20px;
  }
}
@media (min-width: 1220px) {
  .features__list {
    grid-template-columns: auto auto auto auto;
    grid-template-rows: 1fr;
  }
}

.features__item {
  display: flex;
  align-items: center;
  position: relative;
  margin-bottom: 20px;
}
.features__item .features__icon {
  margin-right: 20px;
}
@media (min-width: 768px) {
  .features__item {
    width: 280px;
    max-width: 280px;
    height: 280px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0;
  }
  .features__item .features__icon {
    margin-bottom: 30px;
  }
}

@media (min-width: 768px) {
  .features__desc {
    max-width: 240px;
  }
}

.features__number {
  display: none;
}
@media (min-width: 768px) {
  .features__number {
    display: block;
    position: absolute;
    top: 35%;
    right: 0;
    z-index: -1;
    font-family: "Oswald", arial, helvetica, sans-serif;
    font-size: 280px;
    color: #f2f2f2;
  }
}

.promo {
  background-image: url("../img/promo_bg-mobile.jpg");
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo_bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .promo {
    background-image: none;
    background-color: transparent;
  }
}
@media (min-width: 1220px) {
  .promo {
    background-image: linear-gradient(to right, #ffffff 0, #ffffff 50%, #68b738 50%, #68b738 100%), url("../img/promo_bg-desktop.jpg");
    background-color: transparent;
    background-size: 50%;
    margin-top: -100px;
    background-position-x: right;
    margin-bottom: 50px;
    z-index: -1;
  }
}
@media (min-width: 1220px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo_bg-desktop@2x.jpg");
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 20px;
}
@media (min-width: 768px) {
  .promo__wrapper {
    position: relative;
    margin: 0;
    padding: 0 30px;
  }
}
@media (min-width: 1220px) {
  .promo__wrapper {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto auto auto 1fr;
    grid-template-areas: "title image" "desc image" "btn image";
    margin: auto;
    max-width: 1220px;
  }
}

.promo__title {
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-weight: normal;
  color: #ffffff;
  margin: auto;
  margin-top: 27px;
  margin-bottom: 27px;
  max-width: 300px;
  font-size: 2.6rem;
  line-height: 2.6rem;
  text-align: center;
  grid-area: title;
}
@media (min-width: 768px) {
  .promo__title {
    color: #000000;
    font-size: 4.3rem;
    line-height: 4rem;
    max-width: 500px;
    text-align: left;
    margin-top: 50px;
    margin-left: 90px;
  }
}
@media (min-width: 1220px) {
  .promo__title {
    margin-top: 240px;
    font-size: 3rem;
    line-height: 3rem;
    max-width: 400px;
  }
}

.promo__description {
  font-family: "Oswald", arial, helvetica, sans-serif;
  color: #ffffff;
  font-weight: normal;
  text-transform: uppercase;
  line-height: 0.8rem;
  grid-area: desc;
}
@media (min-width: 768px) {
  .promo__description {
    color: #000000;
    margin-left: 90px;
    font-size: 1.2rem;
    margin-bottom: 50px;
    text-align: left;
  }
}

.promo__image {
