}
.promo__image img {
  max-width: 280px;
}
@media (min-width: 768px) {
  .promo__image {
    order: 1;
    margin: auto;
    margin-top: -225px;
    z-index: -1;
  }
  .promo__image img {
    max-width: 709px;
  }
}
@media (min-width: 1220px) {
  .promo__image {
    order: 0;
    z-index: 1;
    margin: 0;
    margin-top: 150px;
    margin-left: -150px;
  }
}

.promo__button {
  margin-bottom: -150px;
  grid-area: btn;
}
@media (min-width: 768px) {
  .promo__button {
    margin-left: 90px;
    margin-right: 120px;
    max-width: 200px;
    padding: 20px 26px;
    margin-bottom: 150px;
  }
}

.results {
  position: relative;
  padding-top: 27px;
  padding-bottom: 36px;
  background-color: #eaeaea;
}
@media (min-width: 768px) {
  .results {
    padding-top: 43px;
    padding-bottom: 0;
    background-color: #ffffff;
    border-top: 1px solid #e7e7e7;
  }
  .results::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 462px;
    background-color: #eaeaea;
  }
}
@media (min-width: 768px) and (min-width: 1220px) {
  .results::before {
    background-size: 100% 100%;
    background-image: linear-gradient(to right, #f2f2f2, #f2f2f2 calc(50% + 200px), #eaeaea calc(50% + 200px));
  }
  .results--before::before {
    background-color: #f2f2f2;
    background-image: none;
  }
  .results--after::before {
    background-color: #eaeaea;
    background-image: none;
  }
}
@media (min-width: 1220px) {
  .results {
    padding-top: 0;
    border-top: none;
  }
}

.results__container {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1220px) {
  .results__container {
    display: grid;
    grid-template-columns: 436px 656px;
    grid-auto-rows: min-content min-content 1fr;
    justify-content: space-between;
    grid-template-areas: "title slider" "description slider" "list slider";
  }
}

.results__title {
  grid-area: title;
  margin-bottom: 33px;
  font-size: 3rem;
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-weight: normal;
  line-height: 2rem;
}
@media (min-width: 768px) {
  .results__title {
    margin-bottom: 64px;
  }
}
@media (min-width: 1220px) {
  .results__title {
    margin-top: 58px;
    margin-bottom: 147px;
  }
}

.results__description {
  grid-area: description;
  margin: 0;
  margin-bottom: 25px;
  font-family: arial, helvetica, sans-serif;
  font-size: 14px;
  line-height: 18px;
}
@media (min-width: 768px) {
  .results__description {
    margin-bottom: 52px;
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1220px) {
  .results__description {
    margin-bottom: 68px;
  }
}

.results__list {
  grid-area: list;
  display: grid;
  grid-template-columns: repeat(2, max-content);
  justify-content: center;
  gap: 35px 30px;
  margin: 0;
  margin-bottom: 38px;
}
@media (min-width: 768px) {
  .results__list {
    grid-template-columns: repeat(2, max-content) 1fr;
    gap: 42px;
    margin-bottom: 60px;
  }
}
@media (min-width: 1220px) {
  .results__list {
    grid-template-columns: repeat(2, max-content);
    grid-template-rows: min-content 1fr;
    justify-self: flex-start;
    row-gap: 59px;
    margin-bottom: 20px;
  }
}

.results__item--bordered {
  position: relative;
  grid-column: span 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 124px;
  height: 56px;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
}
@media (min-width: 768px) {
  .results__item--bordered {
    width: 162px;
    height: 72px;
  }
}
.results__item--clean {
  grid-column: span 2;
  text-align: center;
  border: none;
}
@media (min-width: 768px) {
  .results__item--clean {
    grid-column: span 1;
    padding-top: 11px;
  }
}
@media (min-width: 1220px) {
  .results__item--clean {
    grid-column: span 2;
    display: flex;
    padding-top: 0;
  }
}

.results__value {
  margin: 0;
  font-size: 24px;
  font-family: "Oswald", arial, helvetica, sans-serif;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .results__value {
    font-size: 30px;
  }
}

.results__item--bordered .results__name {
  position: absolute;
  bottom: -16px;
  max-width: 90px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
  font-family: arial, helvetica, sans-serif;
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  text-transform: lowercase;
  background-color: #eaeaea;
}
@media (min-width: 768px) {
  .results__item--bordered .results__name {
    max-width: 194px;
    bottom: -5px;
    font-size: 14px;
    line-height: 1;
    background-color: #ffffff;
  }
}
@media (min-width: 1220px) {
  .results__item--bordered .results__name {
    background-color: #f2f2f2;
  }
}

@media (min-width: 1220px) {
  .results--before .results__name {
    background-color: #f2f2f2;
  }
}

@media (min-width: 1220px) {
  .results--after .results__name {
    background-color: #eaeaea;
  }
}

.results__item--clean .results__name {
  display: inline-block;
  font-family: "Oswald", arial, helvetica, sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .results__item--clean .results__name {
    display: block;
    margin-bottom: 10px;
    font-size: 20px;
    text-align: right;
  }
}
@media (min-width: 1220px) {
  .results__item--clean .results__name {
    margin-bottom: 0;
    margin-right: 64px;
  }
}

.results__item--clean .results__value {
  display: inline-block;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .results__item--clean .results__value {
    display: block;
    font-size: 20px;
    text-align: right;
  }
}

.results__slider {
  grid-area: slider;
  align-self: center;
  margin-left: -20px;
  margin-right: -20px;
}
@media (min-width: 768px) {
  .results__slider {
    align-self: center;
    margin-left: -30px;
    margin-right: -30px;
  }
}
@media (min-width: 1220px) {
  .results__slider {
    align-self: flex-end;
    margin-left: 16px;
    margin-right: 0;
  }
}

.slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
}
@media (min-width: 768px) {
  .slider {
    position: relative;
    width: 708px;
    height: 656px;
  }
}

.slider__container {
  position: relative;
  width: 100%;
  height: 250px;
  margin-bottom: 17px;
}
@media (min-width: 768px) {
  .slider__container {
