.plan-form__label--radio {
  display: block;
  position: relative;
  padding-left: 45px;
  margin-bottom: 27px;
  cursor: pointer;
  user-select: none;
}

.plan-form__label--radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.plan-form__radio {
  position: absolute;
  top: -6px;
  left: 0;
  height: 30px;
  width: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}

.plan-form__label--radio:hover input ~ .plan-form__radio {
  border: 1px solid #bababa;
  background-color: #68b738;
}

.plan-form__label--radio:active input ~ .plan-form__radio {
  border: 1px solid #bababa;
  opacity: 0.3;
}

.plan-form__radio::after {
  content: "";
  position: absolute;
  display: none;
}

.plan-form__label--radio input:checked ~ .plan-form__radio::after {
  display: block;
}

.plan-form__label--radio .plan-form__radio::after {
  top: 8px;
  left: 8px;
  width: 13.5px;
  height: 13.5px;
  border-radius: 50%;
  background: #68b738;
}

.plan-form__contacts {
  margin-top: 26px;
