}
.plan-form__contacts .plan-form__title {
  padding: 0;
  max-width: 250px;
  margin-bottom: 35px;
  position: relative;
}
.plan-form__contacts .plan-form__title::before {
  content: " ";
  position: absolute;
  top: 55%;
  left: calc((1220px - 100vw) / 2);
  width: 100vw;
  height: 2px;
  background-color: #68b738;
  z-index: -1;
}
@media (min-width: 768px) {
  .plan-form__contacts .plan-form__title {
