.plan-form__label--checkbox {
  display: block;
  position: relative;
  padding-left: 40px;
  margin-bottom: 25px;
  cursor: pointer;
  font-size: 1.1rem;
  user-select: none;
}

.plan-form__label--checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.plan-form__checkmark {
  position: absolute;
  top: -3px;
  left: 0;
  height: 26px;
  width: 26px;
  border: 1px solid #cdcdcd;
}

.plan-form__label--checkbox:hover input ~ .plan-form__checkmark {
  border: 1px solid #bababa;
  background-color: #68b738;
}

.plan-form__label--checkbox:active input ~ .plan-form__checkmark {
  border: 1px solid #bababa;
  opacity: 0.3;
}

.plan-form__checkmark::after {
  content: "";
  position: absolute;
  display: none;
}

.plan-form__label--checkbox input:checked ~ .plan-form__checkmark::after {
  display: block;
}

.plan-form__label--checkbox .plan-form__checkmark::after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid #68b738;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

